
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.name-input {
    width: 20%;
    padding: 10px 18px;
    font-size: 18px;
    margin-bottom: 16px;
    border-radius: 5px;
}
.select-input {
    width: 20%;
    padding: 10px 18px;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    overflow: hidden;
}
.select-input select {
    padding: 5px 8px;
    width: 130%;
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: transparent;
    background-image: none;
    -webkit-appearance: none;
}
.select-input select:focus {
    outline: none;
}
.errors {
    width: 20%;
    padding: 10px 18px;
    font-size: 18px;
    margin-bottom: 16px;
    text-align: center;
    color: red;
}
.button {
    background-color: #4CAF50; /* Green */
    border: none;
    border-radius: 5px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.button :disabled {
    background-color: gray;
    color: slategray;
}

.question-input {
  width: 60%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;
  border-radius: 5px;
}
table, td, tr {
  table-layout: fixed;
  width: 100%;
  text-align: left;
}
.question-time {
  text-align: left;
  float: left;
  font-style: italic;
  font-size: 14px;
}
.question-author {
  float: right;
  font-style: italic;
  font-size: 14px;
}
.question-vote {
  text-align: center;
  padding: 0px 40px;
}
.question-box {
  font-size: 18px;
  border: 1px solid black;
  border-radius: 5px;
  padding: 0px 20px;
}
.upvote-button {
  border: 0px;
}
.upvote-img {
  background-color: coral;
}
